body {
  font-family: 'Orbitron', sans-serif;
  font-weight: 400;
  line-height: 1.7;
  color: $color-secondary;
  /* padding: 30px; */
}

.heading-primary {
  text-transform: uppercase;
  animation: moveInLeft 1s ease-out;
}

.heading-sub {
  text-transform: uppercase;
  animation: moveInRight 1s ease-out;
}

.floats {
  color: $color-red;
}

